.cards-item {
  margin-bottom: 24px;
    list-style: none;
    width: 45%;
    height: 30em;
}

.link__cards-item {
  display: flex;
  flex-flow: column;
  width: 100%;
  box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17);
  -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  border-radius: 10px;
  overflow: hidden;
  text-decoration: none;
}

.pic-wrap__cards-item {
  position: relative;
  width: 100%;
  padding-top: 67%;
  overflow: hidden;
}



.pic-wrap__cards-item::after {
  content: attr(data-category);
  position: absolute;
  bottom: 0;
  margin-left: 10px;
  padding: 6px 8px;
  max-width: calc((100%) - 60px);
  font-size: 12px;
  font-weight: 700;
  color: #fff;
  background-color: #1f98f4;
  box-sizing: border-box;
}

.img__cards-item {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: block;
  width: 100%;
  max-width: 100%;
  height: 100%;
  max-height: 100%;
  object-fit: cover;
  transition: all 0.2s linear;
}

.img__cards-item:hover {
  transform: scale(1.1);
}

.info__cards-item {
  padding: 20px 30px 30px;
}

.text__cards-item {
  color: #252e48;
  font-size: 18px;
  line-height: 24px;
}


@media only screen and (max-width: 1024px) {
  .cards-item {
    margin-bottom: 2rem;
    list-style: none;

  }
}