@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'PT Sans', sans-serif;
}


.home,
.services,
.products,
.sign-up {
  display: flex;
  height: 90vh;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
}

.cards {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: #fff;
}

.cards > h2 {
  text-align: center;
  margin-bottom: 2rem;
  margin-top: 2rem;
}

.container__cards {
  display: flex;
  align-items: center;
  width: 90%;
}


.items__cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: baseline;
  border-radius: 10px;
}


@media only screen and (min-width: 1024px) {
  .items__cards {
    display: flex;
  }
}

.cards-item {
  margin-bottom: 24px;
    list-style: none;
    width: 45%;
    height: 30em;
}

.link__cards-item {
  display: flex;
  flex-flow: column;
  width: 100%;
  box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17);
  -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  border-radius: 10px;
  overflow: hidden;
  text-decoration: none;
}

.pic-wrap__cards-item {
  position: relative;
  width: 100%;
  padding-top: 67%;
  overflow: hidden;
}



.pic-wrap__cards-item::after {
  content: attr(data-category);
  position: absolute;
  bottom: 0;
  margin-left: 10px;
  padding: 6px 8px;
  max-width: calc((100%) - 60px);
  font-size: 12px;
  font-weight: 700;
  color: #fff;
  background-color: #1f98f4;
  box-sizing: border-box;
}

.img__cards-item {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: block;
  width: 100%;
  max-width: 100%;
  height: 100%;
  max-height: 100%;
  object-fit: cover;
  transition: all 0.2s linear;
}

.img__cards-item:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.info__cards-item {
  padding: 20px 30px 30px;
}

.text__cards-item {
  color: #252e48;
  font-size: 18px;
  line-height: 24px;
}


@media only screen and (max-width: 1024px) {
  .cards-item {
    margin-bottom: 2rem;
    list-style: none;

  }
}
video {
  object-fit: cover;
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: -1;
}

.container__hero {
  /* background: url('/images/img-home.jpg') center center/cover no-repeat; */
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
  object-fit: contain;
}

.container__hero > h1 {
  display: flex;
  justify-content: center;
  width: 90%;
  text-align: center;
  color: #fff;
  font-size: 5rem;
}

.container__hero > p {
  display: flex;
  justify-content: center;
  width: 90%;
  text-align: center;
  color: #fff;
  font-size: 4rem;
}

.box-btns__hero,
.btn__hero {
  display: flex;
  flex-direction: column;
  height: 20vh;
  justify-content: space-between;
  align-items: center;
}

.fa-play-circle {
  margin-left: 4px;
}

@media screen and (max-width: 960px) {
  .container__hero > h1 {
    display: flex;
    width: 90%;
    text-align: center;
    font-size: 4rem;
  }
  .container__hero > p {
    display: flex;
    width: 90%;
    text-align: center;
    font-size: 3rem;
  }
}

@media screen and (max-width: 768px) {
  .container__hero > h1 {
    display: flex;
    width: 90%;
    text-align: center;
    font-size: 3.2rem;
  }

  .container__hero > p {
    display: flex;
    width: 90%;
    text-align: center;
    font-size: 2rem;
  }

  .btn-mobile {
    display: block;
    text-decoration: none;
  }
}

:root {
    --primary: #ffffff;
}

:root {
  --primary: #fff;
}

.btn {
  padding: 8px 20px;
  border-radius: 2px;
  outline: none;
  border: none;
  cursor: pointer;
}

.btn--primary {
  background-color: #fff;
  background-color: var(--primary);
  color: #242424;
  border: 1px solid #fff;
  border: 1px solid var(--primary);
}

.btn--outline {
  background-color: transparent;
  color: #fff;
  padding: 8px 20px;
  border: 1px solid #fff;
  border: 1px solid var(--primary);
  transition: all 0.3s ease-out;
}

.btn--medium {
  padding: 8px 20px;
  font-size: 1rem;
}

.btn--large {
  padding: 12px 26px;
  font-size: 1.5rem;
}

.btn--large:hover,
.btn--medium:hover {
  transition: all 0.3s ease-out;
  background: #fff;
  color: #242424;
  transition: 250ms;
}
