}
.sign-up {
  background-image: url(/travel-website/home/static/media/img-8.fabdf94d.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  color: #fff;
  font-size: 100px;
} 
.services {
  background-image: url(/travel-website/home/static/media/img-2.bb238fc1.jpg);
  background-position: center;
  background-size: cover;
